.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}


#bg-img {
    width: 100%;
    height: 100;
    -webkit-clip-path: polygon(0 0, 51% 0, 100% 0, 100% 80%, 50% 65%, 0 80%);
            clip-path: polygon(0 0, 51% 0, 100% 0, 100% 80%, 50% 65%, 0 80%);
    position: absolute;
    z-index: -1;
}

#intro-card {
    font-family: 'Jockey One', sans-serif;
    float: left;
    padding: 5rem;
    border: 0;
}
#info-card {
    text-align: right;
    font-family: 'Jockey One', sans-serif;
    font-size: large;
    margin-top: 20px;
    border: 0;
    padding: 3rem;
}
#resume {
    text-align: justify;
    font-size: larger;
    color: white;
}
#intro {
    background-size: cover;
  }
  .welcome {
    text-align: left;
}

#bioContainer {
    border: none;
}
#dividerOne {
    width: 100%;
    height: 250px;
}
#dividerTwo {
    width: 100%;
    height: 150px;
}

#portfolio {
    text-align: center;
    font-family: 'Arial Narrow Bold', sans-serif;
}
.card-img-top {
    border-color: solid 1px black;
}
#upNext {
    text-align: center;
    font-weight: bolder;
}

